.section {
  background-color: #d5e2ef;
}
.profil {
  text-align: left;
}
#mainDiv {
  width: 400px;
  height: 400px;
  margin: 200px auto;
  perspective: 800px;
}
.profile {
  margin-top: 180px;
}
.profil {
  font-size: 4rem;
}
.profil-a {
  font-size: 20px;

  text-align: justify;
}
#boxDiv {
  width: 400px;
  transform-style: preserve-3d;
  animation: rotate 15s linear infinite;
}

#boxDiv div {
  position: absolute;
  width: 400px;
  height: 400px;
  opacity: 0.95;
  color: white;
}

#front {
  transform: translateZ(200px);
  background: black;
  background-image: url(../img/nitk.jpg);
  background-position: center;
  background-repeat: no-repeat;
}

#back {
  transform: rotateY(180deg) translateZ(200px);
  background: #101010;
  background-image: url(../img/lpu.jpeg);
  background-position: center;
  background-repeat: no-repeat;
}

#left {
  transform: rotateY(-90deg) translateX(-200px);
  transform-origin: left;
  background: black;
  background-image: url(../img/node.png);
  background-position: center;
  background-repeat: no-repeat;
}

#right {
  transform: rotateY(90deg) translateX(200px);
  transform-origin: right;
  background: #101010;
  background-image: url(../img/beach.jpg);
  background-position: center;
  background-repeat: no-repeat;
}

#top {
  transform: rotateX(-90deg) translateY(-200px);
  transform-origin: top;
  background: black;
  background-image: url(../img/mongo.png);
  background-position: center;
  background-repeat: no-repeat;
}

#bottom {
  transform: rotateX(90deg) translateY(200px);
  transform-origin: bottom;
  background: #101010;
  background-image: url(http://i72.photobucket.com/albums/i189/MrMagic13/a_logo_white2.png);
  background-position: center;
  background-repeat: no-repeat;
}

@keyframes rotate {
  0% {
    transform: rotateY(0);
  }
  100% {
    transform: rotateY(360deg);
  }
}

.shadow {
  width: 400px;
  height: 400px;
  background: rgba(0, 0, 0, 0.5);
  box-shadow: 0 0 50px 50px rgba(0, 0, 0, 0.5);
  transform: rotateX(90deg) translateZ(-250px) rotateY(180deg) translateX(0px);
}
